import type { WmlDescriptor } from '@wix/editor-velo-cli-comm-api';
import { createVeloCLIServer } from '@wix/editor-velo-cli-comm-api';
import { openBrowser } from '@wix/user-browser';
import { asyncExitHook } from 'exit-hook';
import { CliError, CliErrorCode } from '@wix/cli-error';
import { ErrorViewer } from '@wix/cli-error-reporting';
import { writeCrashReport } from '@wix/cli-debug-log';
import { getDebugLogFilePath } from '@wix/cli-core-definitions';
import { cwd } from 'node:process';
import type { CommandServices } from '@wix/cli-command-contract';
import { createElement } from 'react';
import { renderSync } from '@wix/cli-ui-kit';
import { isHttpError } from '@wix/cli-http-client';
import { getDevEditorBaseUrl } from '../environment.js';
import { createMutableGridApp } from '../gridapp/createGridApp.js';
import { startGridAppUpdater } from '../gridapp/startGridAppUpdater.js';
import { KeyWatcher } from '../key-watcher.js';
import { loadProjectModel, saveProjectModel } from '../model.js';
import { createLogger } from '../logger.js';
import { sitesBiEvents } from '../bi.js';
import { createI18nT } from '../i18n.js';
import { authenticate } from '../authentication.js';
import { syncAllToRevision } from './sync-all-to-revision.js';
import { readWml, readWmlPage, writeWml } from '../files/wml.js';
import { createWmlFileWatcher } from '../files/wml-watcher.js';

export async function dev(
  { biLogger, errorReporter }: CommandServices,
  {
    https = false,
    experimentalWml = false,
  }: {
    https?: boolean;
    experimentalWml?: boolean;
  }
) {
  let model = await loadProjectModel(cwd());
  const authState = await authenticate({
    metaSiteId: model.metaSiteId,
    biLogger,
    errorReporter,
  });

  const t = createI18nT();
  const logger = createLogger({ t });
  const biEvents = sitesBiEvents(biLogger);

  const server = createVeloCLIServer({ https });

  let gridAppId: string;
  try {
    gridAppId = await createMutableGridApp(model.projectFolder, {
      authState,
    });
  } catch (error) {
    const message = isHttpError(error) && error.response?.data?.message;

    throw new CliError({
      code: CliErrorCode.FailedToCreateGridApp({
        error: message,
      }),
      cause: error,
    });
  }

  if (!experimentalWml) {
    await syncAllToRevision(model, { authState }, logger);
  }

  const { close } = startGridAppUpdater(
    model,
    gridAppId,
    {
      authState,
      onError(error) {
        logger.logNewLine();
        errorReporter.reportError(error);
        writeCrashReport(getDebugLogFilePath(model.projectFolder), error);
        renderSync(createElement(ErrorViewer, { error }));
      },
    },
    logger,
    biEvents
  );

  const onServerDisconnected = (devEditorUrl: string) => {
    const message = logger.logNoOpenedTab(devEditorUrl);
    biEvents.messageDisplayed({ model, message });
  };

  const onSyncDevEditorChanges = async (revision: string) => {
    if (revision !== model.revision) {
      const newModel = { ...model, revision };

      await saveProjectModel(newModel);
      const message = logger.logUpdatedLocalCodeToRevision({
        revision: newModel.revision,
      });
      biEvents.messageDisplayed({ message, model });

      if (!experimentalWml) {
        await syncAllToRevision(newModel, { authState }, logger);
      }

      model = newModel;
    }
  };

  const onWmlChange = async (wml: WmlDescriptor[]) => {
    logger.logWmlChangesRecieved(
      wml.map((wmlDescriptor) => wmlDescriptor.pagePointer.id)
    );
    setImmediate(async () => {
      const result = await writeWml(model.projectFolder, wml);
      logger.logWmlWriteResult(result);
    });
  };

  const openLocalEditor = async (serverPort: number) => {
    biEvents.userResponse({
      action: 'Browser opened',
      message: 'Opening the Local Editor',
      model,
    });

    const devEditorUrl = getDevEditorUrl(serverPort);

    if (https) {
      await openBrowser(new URL(`https://localhost:${serverPort}`).href);
    } else {
      await openBrowser(devEditorUrl);
    }
  };

  const getDevEditorUrl = (serverPort: number) => {
    const queryParams = new URLSearchParams({
      localPort: serverPort + '',
      secureSocket: https ? 'true' : 'false',
      experimentalImportExport: experimentalWml ? 'true' : 'false',
    });

    return new URL(
      model.metaSiteId + '?' + queryParams.toString(),
      getDevEditorBaseUrl()
    ).href;
  };

  let keyWatcher: KeyWatcher | undefined;
  try {
    const wml = experimentalWml ? await readWml(model.projectFolder) : [];
    const { port: serverPort } = await server.serve(
      gridAppId,
      getDevEditorUrl,
      wml
    );

    const devEditorUrl = getDevEditorUrl(serverPort);

    server.on('disconnected', () => onServerDisconnected(devEditorUrl));
    server.on('syncDevEditorChanges', onSyncDevEditorChanges);

    let closeWmlFileWatcher;
    if (experimentalWml) {
      server.on('wmlChange', onWmlChange);

      const { close } = createWmlFileWatcher(
        model.projectFolder,
        async (pageId: string) => {
          logger.logWmlLocalFilesChangedForPage(pageId);
          const wmlPageDescriptor = await readWmlPage(
            model.projectFolder,
            pageId
          );
          await server.syncWmlChange([wmlPageDescriptor]);
        }
      );
      closeWmlFileWatcher = close;
    }

    keyWatcher = new KeyWatcher();

    logger.logOpeningBrowser(https);

    await openLocalEditor(serverPort);
    const { ctrlCPromise } = keyWatcher.watch();
    keyWatcher.on('e', () => openLocalEditor(serverPort));

    asyncExitHook(server.close, { minimumWait: 500 });
    await ctrlCPromise;

    if (closeWmlFileWatcher) {
      await closeWmlFileWatcher();
    }
  } finally {
    keyWatcher?.unwatch();
    await Promise.all([server.close(), close()]);
  }
}
