import fs from 'node:fs';
import path from 'node:path';
import type { WmlDescriptor } from '@wix/editor-velo-cli-comm-api';
import { markPathWrittenFromServerNow } from './wml-server-files-recieve-times-store.js';
import { WML_DIR } from './wml-constants.js';

type WmlPageData = WmlDescriptor['data'];
type WmlPageDataFileName = keyof WmlPageData;

async function readExistingFile(filePath: string): Promise<string | null> {
  try {
    return await fs.promises.readFile(filePath, 'utf-8');
  } catch {
    return null;
  }
}

export async function writeWml(
  projectDir: string,
  wmlDescriptors: WmlDescriptor[]
) {
  const wmlDir = path.join(projectDir, WML_DIR);

  await fs.promises.mkdir(wmlDir, { recursive: true });
  const result = await Promise.all(
    wmlDescriptors.map(async (wmlDescriptor) => {
      const pageDir = path.join(wmlDir, wmlDescriptor.pagePointer.id);
      await fs.promises.mkdir(pageDir, { recursive: true });

      const fileWritePromises = Object.keys(wmlDescriptor.data).map(
        async (fileName) => {
          const filenamePath = path.join(pageDir, fileName);
          const existingContent = await readExistingFile(filenamePath);
          if (
            existingContent ===
            wmlDescriptor.data[fileName as WmlPageDataFileName]
          ) {
            return {
              file: filenamePath,
              result: 'skip',
            };
          }

          await fs.promises.writeFile(
            filenamePath,
            wmlDescriptor.data[fileName as WmlPageDataFileName],
            'utf-8'
          );

          markPathWrittenFromServerNow(filenamePath);

          return {
            file: filenamePath,
            result: 'write',
          };
        }
      );

      return Promise.all(fileWritePromises);
    })
  );

  const flatResult = Array.prototype.concat.apply([], result) as Array<{
    file: string;
    result: 'write' | 'skip';
  }>;

  return flatResult.reduce(
    (accamulator, current) => {
      accamulator[current.result].push(current.file);
      return accamulator;
    },
    {
      write: [] as string[],
      skip: [] as string[],
    }
  );
}

async function isExists(somePath: string): Promise<boolean> {
  try {
    await fs.promises.access(somePath);
    return true;
  } catch (error: unknown) {
    if (
      error instanceof Error &&
      (error as NodeJS.ErrnoException).code === 'ENOENT'
    ) {
      return false; // File or directory does not exist
    }
    throw error; // Other errors are thrown
  }
}

export async function readWml(projectDir: string): Promise<WmlDescriptor[]> {
  const wmlDir = path.join(projectDir, WML_DIR);

  if (!(await isExists(wmlDir))) {
    return [];
  }

  const pageDirs = await fs.promises.readdir(wmlDir);

  const pageReadPromises = pageDirs.map(async (pageId) =>
    readWmlPage(projectDir, pageId)
  );

  // Return an array of WmlDescriptors
  return Promise.all(pageReadPromises);
}

export async function readWmlPage(projectDir: string, pageId: string) {
  const pagePath = path.join(projectDir, WML_DIR, pageId);
  const fileNames = await fs.promises.readdir(pagePath);

  const fileReadPromises = fileNames.map(async (fileName) => {
    const filePath = path.join(pagePath, fileName);
    const fileContent = await fs.promises.readFile(filePath, 'utf-8');
    return { [fileName]: fileContent };
  });

  const fileContents = await Promise.all(fileReadPromises);
  // Merge the individual file contents into a single object
  const data: WmlPageData = Object.assign({}, ...fileContents);

  return { data, pagePointer: { id: pageId, type: 'DESKTOP' } };
}
