import path from 'node:path';
import type fs from 'node:fs';
import chokidar from 'chokidar';
import { getLastTimePathReceivedFromServer } from './wml-server-files-recieve-times-store.js';
import {
  WML_DIR,
  WAIT_TIME_UTNIL_FILE_CONSIDERED_CHANGED_AFTER_SERVER_UPDATE,
} from './wml-constants.js';

export function createWmlFileWatcher(
  projectDir: string,
  pageChangedCallback: (pageId: string) => void
) {
  const directoryToWatch = path.join(projectDir, WML_DIR);
  const watcher = chokidar.watch(directoryToWatch, {
    persistent: true,
    awaitWriteFinish: true,
    ignoreInitial: true,
  });

  const getPageIdFromPath = (somePath: string) =>
    somePath.split(path.sep).at(-2);

  const changeEventHandler = (filePath: string, stats: fs.Stats) => {
    const pageId = getPageIdFromPath(filePath);
    if (!pageId) {
      return;
    }

    if (
      Math.abs(
        stats.mtimeMs - (getLastTimePathReceivedFromServer(filePath) ?? 0)
      ) < WAIT_TIME_UTNIL_FILE_CONSIDERED_CHANGED_AFTER_SERVER_UPDATE
    ) {
      return;
    }

    pageChangedCallback(pageId);
  };

  watcher.on('change', changeEventHandler);

  return {
    close: () => watcher.close(),
  };
}
